/* Reset and base styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    background-color: #f5f7fa;
  }
  
  /* General section styling */
  section {
    padding: 4rem 2rem;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  /* Overview section */
  .overview-section {
    display: flex;
    gap: 3rem;
    align-items: center;
    min-height: 80vh;
    background: linear-gradient(135deg, #e8f4fd 0%, #f5f7fa 100%);
    border-radius: 20px;
    margin: 2rem auto;
  }
  
  .overview-text {
    flex: 1;
  }
  
  .overview-text h1 {
    font-size: 3rem;
    margin-bottom: 1.5rem;
    color: #2c3e50;
    line-height: 1.2;
    font-weight: 700;
  }
  
  .overview-text p {
    font-size: 1.1rem;
    line-height: 1.6;
    color: #5a6c7d;
    margin-bottom: 1.5rem;
  }
  
  .overview-image {
    flex: 1;
    position: relative;
  }
  
  .overview-image img {
    width: 100%;
    height: 500px;
    object-fit: cover;
    border-radius: 15px;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  }
  
  /* Features and Benefits section */
  .features-benefits {
    background: linear-gradient(135deg, #f5f7fa 0%, #e8f4fd 100%);
    text-align: center;
    padding: 5rem 2rem;
    border-radius: 20px;
    margin: 2rem auto;
  }
  
  .features-benefits h2 {
    font-size: 2.5rem;
    color: #2c3e50;
    margin-bottom: 3rem;
    font-weight: 700;
  }
  
  .features-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
    padding: 1rem;
  }
  
  .feature-card {
    background: rgba(255, 255, 255, 0.9);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.05);
    transition: transform 0.3s ease;
    text-align: center;
  }
  
  .feature-card:hover {
    transform: translateY(-5px);
    background: rgba(255, 255, 255, 0.95);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  }
  
  .feature-icon {
    font-size: 2.5rem;
    margin-bottom: 1rem;
    display: inline-block;
  }
  
  .feature-card h3 {
    font-size: 1.5rem;
    color: #2c3e50;
    margin-bottom: 1rem;
    font-weight: 600;
  }
  
  .feature-card p {
    color: #5a6c7d;
    line-height: 1.5;
  }
  
  /* CTA section */
  .cta-section {
    text-align: center;
    background: linear-gradient(135deg, #3498db, #2980b9);
    color: white;
    border-radius: 15px;
    margin: 2rem auto;
    padding: 4rem 2rem;
    box-shadow: 0 15px 30px rgba(41, 128, 185, 0.2);
  }
  
  .cta-section h2 {
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
    font-weight: 700;
  }
  
  .cta-section p {
    font-size: 1.2rem;
    margin-bottom: 2rem;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    line-height: 1.6;
  }
  
  .cta-button {
    display: inline-block;
    padding: 1rem 2rem;
    font-size: 1.2rem;
    font-weight: 600;
    text-decoration: none;
    color: white;
    background: linear-gradient(135deg, #28a745, #218838);
    border-radius: 30px;
    transition: all 0.3s ease;
    cursor: pointer;
    border: 2px solid transparent;
    box-shadow: 0 4px 15px rgba(40, 167, 69, 0.2);
  }
  
  .cta-button:hover {
    transform: scale(1.05);
    box-shadow: 0 6px 20px rgba(40, 167, 69, 0.3);
    background: linear-gradient(135deg, #218838, #28a745);
    color: white;
    border-color: rgba(255, 255, 255, 0.2);
  }
  
  /* Responsive Design */
  @media (max-width: 1024px) {
    .features-grid {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (max-width: 768px) {
    .overview-section {
      flex-direction: column;
      text-align: center;
    }
  
    .overview-text h1 {
      font-size: 2.5rem;
    }
  
    .features-grid {
      grid-template-columns: 1fr;
    }
  
    .cta-section h2 {
      font-size: 2rem;
    }
  }
  
  @media (max-width: 480px) {
    section {
      padding: 2rem 1rem;
    }
  
    .overview-text h1 {
      font-size: 2rem;
    }
  
    .features-benefits h2 {
      font-size: 2rem;
    }
  
    .feature-card {
      margin: 0;
    }
  
    .cta-button {
      width: 90%;
      max-width: 300px;
    }
  }
  