/* Supplier Section Styles */
.supplier-section {
  font-family: 'Roboto', sans-serif;
  color: #333;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Hero Section */
.supplier-section .hero {
  background: linear-gradient(135deg, #1e3c72, #2a5298);
  color: #fff;
  padding: 100px 20px;
  text-align: center;
  border-bottom: 5px solid #f8f9fa;
}

.supplier-section .hero .hero-content h1 {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 20px;
  line-height: 1.2;
}

.supplier-section .hero .hero-content p {
  font-size: 1.2rem;
  margin-bottom: 30px;
  opacity: 0.8;
}

.supplier-section .hero .hero-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.supplier-section .hero .cta-demo,
.supplier-section .hero .cta-experience {
  background-color: #ff7e5f;
  color: white;
  border: none;
  padding: 15px 40px;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 2px solid transparent;
}

.supplier-section .hero .cta-demo:hover,
.supplier-section .hero .cta-experience:hover {
  background-color: #feb47b;
  border: 2px solid #ff7e5f;
}

/* Challenges Section */
.supplier-section .challenges {
  background-color: #f1f1f1;
  padding: 60px 20px;
  text-align: center;
}

.supplier-section .challenges h2 {
  font-size: 2.5rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 30px;
}

.supplier-section .challenges p {
  font-size: 1.15rem;
  color: #555;
  max-width: 800px;
  margin: 0 auto;
}

/* Process Section */
.supplier-section .process {
  padding: 80px 20px;
  background-color: #fff;
  text-align: center;
  border-top: 5px solid #f8f9fa;
}

.supplier-section .process h2 {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 40px;
  font-weight: 600;
}

.supplier-section .process .steps {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 40px;
  justify-items: center;
}

.supplier-section .process .step {
  background-color: #f8f9fa;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.supplier-section .process .step:hover {
  transform: translateY(-10px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
}

.supplier-section .process .step-number {
  font-size: 2.5rem;
  font-weight: bold;
  color: #ff7e5f;
  margin-bottom: 20px;
}

.supplier-section .process .step h3 {
  font-size: 1.4rem;
  color: #333;
  margin-bottom: 15px;
}

.supplier-section .process .step p {
  font-size: 1.1rem;
  color: #555;
}

/* Benefits Section */
.supplier-section .benefits {
  padding: 80px 20px;
  background-color: #f1f1f1;
  text-align: center;
}

.supplier-section .benefits h2 {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 40px;
  font-weight: 600;
}

.supplier-section .benefits .benefit-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 30px;
}

.supplier-section .benefits .benefit {
  background-color: #fff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.supplier-section .benefits .benefit:hover {
  transform: translateY(-10px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
}

.supplier-section .benefits .benefit h3 {
  font-size: 1.6rem;
  color: #007bff;
  margin-bottom: 20px;
}

.supplier-section .benefits .benefit p {
  font-size: 1.1rem;
  color: #555;
  margin-bottom: 20px;
}

.supplier-section .benefits .cta-demo {
  background-color: #ff7e5f;
  color: white;
  padding: 15px 35px;
  font-size: 1rem;
  border-radius: 50px;
  cursor: pointer;
  border: 2px solid transparent;
  transition: all 0.3s ease;
}

.supplier-section .benefits .cta-demo:hover {
  background-color: #feb47b;
  border: 2px solid #ff7e5f;
}

/* Testimonials Section */
.supplier-section .testimonials {
  padding: 80px 20px;
  background-color: #fff;
  text-align: center;
}

.supplier-section .testimonials h2 {
  font-size: 2.5rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 40px;
}

.supplier-section .testimonials .testimonial-container {
  display: flex;
  justify-content: space-between;
  gap: 30px;
  flex-wrap: wrap;
}

.supplier-section .testimonials .testimonial {
  background-color: #f8f9fa;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  flex: 1 1 45%;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.supplier-section .testimonials .testimonial:hover {
  transform: translateY(-10px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
}

.supplier-section .testimonials .testimonial-image-container {
  margin-bottom: 20px;
}

.supplier-section .testimonials .testimonial-image {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 20px;
}

.supplier-section .testimonials p {
  font-size: 1.1rem;
  color: #333;
  margin-bottom: 15px;
}

.supplier-section .testimonials span {
  font-size: 1.1rem;
  color: #007bff;
}

/* Categories Section */
/* Categories Section */
.supplier-section .categories {
  padding: 60px 20px;
  background-color: #f1f1f1;
  text-align: center;
  border-top: 5px solid #f8f9fa;
}

.supplier-section .categories h2 {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 40px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.supplier-section .categories ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
}

.supplier-section .categories ul li {
  font-size: 1.3rem;
  color: #333;
  background-color: #fff;
  padding: 15px 25px;
  border-radius: 30px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.supplier-section .categories ul li:hover {
  transform: translateY(-10px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
  background-color: #ff7e5f;
  color: white;
}

.supplier-section .categories ul li:nth-child(even) {
  background-color: #e9ecef;
}

.supplier-section .categories ul li:nth-child(odd) {
  background-color: #fff;
}

.supplier-section .categories ul li:hover {
  background-color: #feb47b;
}
/* Final CTA Section */
.supplier-section .cta-final {
  padding: 80px 20px;
  text-align: center;
  background: linear-gradient(135deg, #ff7e5f, #feb47b);
  color: white;
}

.supplier-section .cta-final h2 {
  font-size: 2.5rem;
  font-weight: 600;
  margin-bottom: 30px;
}

.supplier-section .cta-final .cta-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.supplier-section .cta-final .cta-demo,
.supplier-section .cta-final .cta-free {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 15px 35px;
  font-size: 1rem;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.supplier-section .cta-final .cta-demo:hover,
.supplier-section .cta-final .cta-free:hover {
  background-color: #0056b3;
}
